
<template>
  <div class="side-nav">
    <div v-for="(title,index) in (Object.keys(data))" :key="index" class="group-container">
      <p class="side-nav-title">{{title}}</p>
      <div class="side-nav-items" v-for="(nav,index2) in data[title]" :key="index2">
        <template v-if="nav.desc">
          <router-link :class="$route.name===nav.name ? 'active' : ''" v-if="nav.name" :to="{name: nav.name}">{{nav.desc}}</router-link>
          <p v-else class="side-nav-group">{{nav.desc}}</p>
          <div v-for="(item, index3) in nav.items" :key="index3">
            <router-link :to="{name: item.name}" :class="$route.name===item.name ? 'active' : ''" class="slid-nav-component">{{item.desc}}</router-link>
          </div>
        </template>
      </div>
    </div>
  </div>
</template>

<script>
  import navConf from './nav.conf.json'
  export default {
    data () {
      return {
        data: navConf
      }
    },
    mounted() {
      console.log(123)
    }
  }
</script>
<style lang="scss">
  .side-nav{
    position: fixed;
    height: 100%;
    top: 0;
    bottom: 0;
    overflow: scroll;
    width: 215px;
    margin: 80px 0 32px;
    padding: 20px 0;
    color: #3F536E;
    background-color: #fff;
    z-index: 99;
    .group-container{
      margin-bottom: 32px;
    }
    .side-nav-title{
      padding: 0 24px 8px;
      color: #8DABC4;
      font-size: 12px;
      font-weight: bold;
      letter-spacing: 1px;
      text-transform: uppercase;
    }
    .side-nav-items{
      font-size: 14px;
      font-weight: normal;
      line-height: 1.8;
      a{
        display: block;
        position: relative;
        padding: 8px 24px;
        color: #3F536E;
        font-weight: normal;
        line-height: 1.5;
        cursor: pointer;
      }
      .side-nav-group{
        display: block;
        position: relative;
        padding: 6px 0 6px 24px;
        color: #2C405A;
        font-weight: bold;
      }
      .slid-nav-component{
        display: block;
        position: relative;
        padding: 6px 24px 6px 32px;
        color: #616367;
        font-size: 14px;
      }
      .active{
        color: #3FAAF5;
      }
    }
  }
</style>



